*{
  margin : 0;
  padding : 0;
  font-family: 'Inter', sans-serif;
}

body {
    background-color: #23252c;
}

.container {
    width: 317px;
    margin: 20px auto;
    background-color: #1a1b21;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  
}

img {
    width: 317px;
    height: 317px;
    object-fit :cover;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.profile-container {
    text-align: center;
}
.profile-desc-container {
    margin-top: 25px;
}

.profile-container h3 {
    font-size: 25px;
    color: white;
    font-weight: 700;
    margin-bottom: 5px;

}

.profile-container h4 {
    font-size: 12.8px;
    color: #f3bf99;
    margin-bottom: 5px;
}
.profile-container p {
    font-size: 10.24px;
    color: #f5f5f5;
    margin-bottom: 15px;
}


button {
   width: 247px;
   height: 40px; 
   font-size: 14px;
   border-radius: 5px;
   box-sizing: border-box;
   display: flex;
   align-items: center;
   justify-content: center;
   margin: 0 auto;
   border-style: none;
}

button img {
    width: 14px;
    height: 14px;
    margin-right : 5px;
}

.content-container {
    margin-top: 30px;
    padding: 0 35px;
}

.content-container h3 {
    color: #f5f5f5;
    font-weight: bold;
    size: 16px;
    margin-bottom: 5px;
}
.content-container p {
    color: #dcdcdc;
    font-size: 10px;
}

.social-container {
    width: 317px;
    height: 64px;
    background-color: #161619 ;
    margin-top: 30px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}